import axios from "axios";
const BASE_URL = 'https://genius-song-lyrics1.p.rapidapi.com/chart/songs/'
const headers = {
  'X-RapidAPI-Key': '251bebe71amshc2bb86f654ff9c3p16a485jsn93c4a361d9c1',
  'X-RapidAPI-Host': 'genius-song-lyrics1.p.rapidapi.com'
}

export const GeniusService = {
  async getChartSongs(count: number, page: number) {
    const response = await axios.get(`${BASE_URL}`, {
      headers: headers,
      params: {
        per_page: count,
        page: page
      },
    });
    return response.data.chart_items
  },
}
