import axios from "axios";
const BASE_URL = 'https://soundcloud-scraper.p.rapidapi.com/v1'

const headers = {
    'X-RapidAPI-Key': '251bebe71amshc2bb86f654ff9c3p16a485jsn93c4a361d9c1',
    'X-RapidAPI-Host': 'soundcloud-scraper.p.rapidapi.com'
}

export const SpotyfyService = {
    async getHomePage() {
        const response = await axios.get(`${BASE_URL}/home/details`, { headers });
        return response.data.selections
    },
    async getPlayList(id: string) {
        const response = await axios.get(`${BASE_URL}/playlist/tracks`, {
            headers,
            params: {
                playlist: id
            }});
        return response.data.selections
    },
}
