import { useState, useEffect } from 'react'
import { GeniusService } from '../../core/services/genius.service';
import { IChartSong, IPlaylist } from '../../core/interfeces/genius.interfece';
import GroopPlayList from '../../components/GroopPlayList';
import ChartSongs from './components/ChartSongs';
import { SpotyfyService } from '../../core/services/spotyfy.service';
import { IHomeData } from '../../core/interfeces/spotyfy.interfece';

const Home = () => {
    const [list, setList] = useState<IHomeData[]>();
    const [chartList, setChartList] = useState<IChartSong[]>()
    useEffect(() => {
        const fetchCategoriesList = async () => {
            const ChartData = await GeniusService.getChartSongs(11, 1)
            setChartList(ChartData)
            const HomeData = await SpotyfyService.getHomePage()
            setList(HomeData)
        }
        fetchCategoriesList()
    }, [])
    return (
        <>
            {chartList && (
                <ChartSongs {...{ chartList }} />
            )}
            {list && list.map((playlist) => (
                <GroopPlayList title={playlist.title} playLists={playlist.items} />
            ))}
        </>
    )
}

export default Home