import { FC } from "react"
import { IItem } from "../core/interfeces/spotyfy.interfece"
import CardPlayList from "./CardPlayList"
import styles from './styles/GroopPlayList.module.scss';

interface GroopPlayListProps {
    playLists: IItem[]
    title: string
}
const GroopPlayList: FC<GroopPlayListProps> = ({ playLists, title }) => {
    return (
        <>
            <h2 className={styles.name}>{title}</h2>
            <div className={styles.container}>
                {playLists.slice(0, 5).map((item) => (
                    <CardPlayList
                        key={item.id}
                        id={item.id}
                        image={item.artworkUrl}
                        name={item.title}
                        description={item.description} />
                ))}
            </div>
        </>
    )
}

export default GroopPlayList